import { NativeModules } from 'react-native';

export default async (params: any): Promise<any> => {
    return new Promise((resolve, reject) => {
        try {
            const result = NativeModules.KRNBasic.setSlideBack(params);
            resolve({
                result: 1,
                data: result,
            });
        } catch (err) {
            reject({ code: -1, message: `failed to setSlideBack: ${err}` });
        }
    });
};
